@import url(https://fonts.googleapis.com/css2?family=Estonia&family=Pacifico&family=Saira+Condensed:wght@200&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

#map {
    height: 180px;
}

header {
    background-color: rgb(11, 19, 14);
    /* background-color: rgb(4, 53, 22); */
    position: fixed;
    width: 280px;
    padding: 2rem;
    bottom: 0;
    left: 0;
    top: 0;
    z-index: 0;
}

li {
    list-style-type: none;
    font-size: 1.5rem;
    margin-left: -2rem;
    color: rgb(255, 255, 255);
    font-family: 'Saira Condensed', sans-serif;
}

.UserPanel {
    text-align: center;
    color: rgb(255, 255, 255);
    font-size: 2rem;
    margin-top: -2rem;
}

.PicStyle {
    font-size: 5rem;
    background-color: rgb(255, 255, 255);
    border-radius: 50%;
    margin-bottom: -15rem;
    display: inline-block;
    height: 7rem;
    width: 7rem;
}

.AppLogo {
    font-size: 3rem;
    margin: -1rem;
}

.TableClass {
    font-size: 2rem;
}

.AdminContentPageStyle {
    position: fixed;
    padding: 2rem;
    bottom: 0;
    right: 0;
    top: 0;
    text-align: center;
}

.AboutPageStyle {
    position: fixed;

    bottom: 0;
    right: 0;
    top: 0;
    padding: 20rem;
}

.TeammateStyle {
    background-color: rgb(109, 104, 104);
    border-radius: 50%;
    padding: 5rem 10rem;
    margin: 0.5rem;
    text-align: justify;
}

.TeamPicStyle {
    font-size: 4.1rem;
}

.TeamFontStyle {
    font-family: 'Estonia', cursive;
}

strong {
    font-size: 1.5rem;
}

.TeamHandle {
    background-color: rgba(156, 111, 156, 0.705);
}

.TestPanel {
    background-color: rgba(156, 111, 156, 0.705);
    text-align: left;
    color: white;
    padding: 0.5rem;
    margin: 0.1rem;
}

